<template>
    <div>
        <div class="pointer" @click="()=>{
              func()
            }">  
       <div class="realDataBtn">
           <img src="static/imgs/realData.png" alt="">
       </div>
   </div>
    </div>
</template>
<script>
export default {
    data(){
        return{}
    },
    props:["func"]
}
</script>
<style scoped>
.pointer div{
    animation: anima-pointer 2s infinite;
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -1.4rem;
    height: 2.8rem;
    width: 2.8rem;
    /* background: url("../../../static/img/toBottom.png") -63px 0; */
}
 
/* animation*/
@keyframes anima-pointer
{
    0%   {opacity:0;bottom:0}
    100%  {opacity:1;bottom:10px;}
}
.realDataBtn{
    border-radius: 50%;
    height: 48px;
    width: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: rgb(53, 114, 254) */
}
.realDataBtn img{
    width: 36px;
    height: 36px;
}
</style>


