<template>
  <div style="position:relative">
    <div id="containerHome" :style="height"></div>
    <div id="panel"></div>
    <div class="MarkerLabel">
      <img src="static/img/mapBottom.png" />
      <!-- <div v-for="(item,index) in itemListImage" :key="index" v-if="false">
        <div class="imageSize">
          <span>{{item.name}}</span>
          <img :src="item.img" alt />
        </div>
      </div>-->
    </div>
    <!-- <div class="positionAbsolute" v-if="$store.state.btnStatus">
      <LeftNavigation :style="transform" />
    </div>
    <div class="positionAbsolute" v-if="!$store.state.btnStatus">
      <LeftView />
    </div>-->
    <div class="positionRight" ref="rightbtn">
      <RightNavigation ref="websocket" :warningData="DataWarning" />
    </div>
    <!-- <div class="bottomMap" v-if="!opened">
      <MapBottom :func="clickToShow" />
    </div>-->
    <!-- <faceBook :showBtn="showBtn" /> -->
    <div class="gelocation">
      <div @click="toMapGelocation" class="imageSizeGELOCATION">
        <img src="static/imgs/mapGelocation.png" alt />
      </div>
    </div>
    <van-dialog v-model="showDialog" :title="warningTitle==='待处理'?'预警内容':'处理详情'">
      <div class="flexDialog BORDER">
        <span>超标因子</span>
        <span style="color:blue">浓度值</span>
      </div>
      <div v-for="(item,index) in  warningData" :key="index">
        <div class="flexDialog">
          <span>{{item.name===''?'设备':item.name}}</span>
          <span style="color:red">{{item.value}}{{item.name==='pH'||item.name===''?'':' mg/L'}}</span>
        </div>
      </div>
    </van-dialog>
    <mu-container>
      <mu-bottom-sheet :open.sync="opened">
        <RealDataComponent ref="realData" :id="id" />
      </mu-bottom-sheet>
    </mu-container>
    <van-overlay :show="showover" @click="showover = false" class="overlay">
      <van-loading type="spinner" color="#1989fa" />
    </van-overlay>
    <van-dialog
      v-model="showUpdataApp"
      show-cancel-button
      @cancel="cancelUpdateApp"
      @confirm="confirmUpdate"
    >
      <!-- <img src="https://img.yzcdn.cn/vant/apple-3.jpg" /> -->
      <div class="updataHeader">
        <span>版本更新</span>
      </div>
      <div class="boxUpdata">
        <div v-if="showVersionUpdate">
          <div class="heightStyle">
            <span>更新的版本：</span>
            <span>{{versionData.version}}</span>
          </div>
          <div class="heightStyle" style="height:30px;">
            <span>版本解决了以下的问题：</span>
          </div>
          <div v-for="(item,index) in versionData.description" :key="index">
            <div class="heightStyle">
              <span>{{index+1}}、</span>
              <span>{{item}}</span>
            </div>
          </div>
        </div>
        <div v-if="!showVersionUpdate">
          <div class="heightStyle">
            <span>当前为最新版本：</span>
            <span style="color:rgb(50, 142, 251)">{{version}}</span>
          </div>
        </div>
      </div>
    </van-dialog>
    <van-action-sheet
      style="z-index:999"
      v-model="showApp"
      :actions="actions"
      cancel-text="取消"
      @cancel="onCancel"
      @select="onSelect"
      description="选择市场安装，请确保已经安装安智市场"
    />
    <!-- <van-popup v-model="showMapData" position="bottom" :style="{ height: '20%' }" style="z-index:9999" /> -->
  </div>
</template>
<script>
import faceBook from "@/components/facebook";
import AppBace from "../../../static/ajax";
import MarkerPicStation from "../../../static/js/map/markerCont";
import LeftNavigation from "../../mapComponents/leftNavigation/index";
import RightNavigation from "../../mapComponents/rightNavigation/index";
// import HomeDataTable from "../../components/HomeDataTable/index";
import Coordtransform from "../../../static/js/conmonGogeChange.js";
import MapBottom from "../../mapComponents/mapBottom/index";
import LeftView from "../../mapComponents/leftView/index";
import RealDataComponent from "../../mapComponents/realDataComponent/index";
import Vue from "vue";
import { NoticeBar, Popup } from "vant";
import { Toast, Overlay, Loading } from "vant";
import { Dialog } from "vant";
Vue.use(NoticeBar)
  .use(Dialog)
  .use(Overlay)
  .use(Loading)
  .use(Popup);
let map = "";
let infoWindow = "";
var arrLine = [];
var myWebsocket = "";
var infoWindowStation = "";
var driving = "";
// import Vue from "vue";
// var _this = this;
export default {
  name: "realMapComponents",
  data() {
    return {
      timerOut:"",
      showover: false,
      showMapData: false,
      mobileStation: [],
      DataDevice: [],
      DataWarning: [],
      warningScoller: "",
      rightShow: true,
      transform: "",
      showBtnMenu: true,
      height: "",
      listLng: [],
      token: "",
      polyline: null,
      path: [],
      carMarkerCreateFlag: false,
      carMarker: null,
      pathWeb: "ws://47.112.16.168:8802/websocket/",
      // pathWeb: "ws://192.168.50.77:8802/websocket/",
      socket: false,
      opened: false,
      openPointAddress: true,
      bounds: null,
      showDialog: false,
      warningData: [],
      warningTitle: "",
      DataTaskId: [],
      DataTask: [],
      id: "",
      mapInfo: [],
      user: "",
      showVersionUpdate: false,
      versionData: [],
      showApp: false,
      actions: [{ name: "市场更新" }, { name: "本地更新" }],
      appName: "",
      showUpdataApp: false,
      version: ""
    };
  },
  created() {
    this.user = sessionStorage.getItem("user");
  },
  methods: {
    onCancel() {
      this.showApp = false;
      // Toast('cancel');
    },
    confirmUpdate() {
      // 去到应用市场
      // AppUpdata.updataApp();
      this.$store.state.upDateStatus = true;
      this.showApp = true;
    },
    cancelUpdateApp() {
      this.$store.state.upDateStatus = true;
    },
    onSelect(item) {
      if (item.name == "市场更新") {
        AppUpdata.updataApp();
      } else {
        var http = this.appName;
        // var http = "8886/group1/M00/00/01/rBLi414YOk-ACLArASIf1vSN0Rk764.apk";
        window.location.href = "http://47.112.16.168:" + http;
      }
    },
    // 手动更新
    async upadataFunc() {
      var _this = this;
      if (
        localStorage.getItem("version") === null ||
        localStorage.getItem("version") === undefined ||
        localStorage.getItem("version") === ""
      ) {
        cordova.getAppVersion.getVersionNumber().then(function(version) {
          //获取当前app的版本号；
          // $scope.now_version = version;
          localStorage.setItem("version", version);
        });
      }
      if (this.$store.state.upDateStatus === false) {
        var token = sessionStorage.getItem("token");
        var url = "/table/appVersion";
        var res = await AppBace.request(url, "get", {}, token);

        var versionApp = localStorage.getItem("version");
        this.version = versionApp;
        if (
          res.data.data !== null &&
          res.data.data !== undefined &&
          res.data.data !== ""
        ) {
          var arr = res.data.data.version.split(".");

          var arr1 = versionApp.split(".");
          var currentVersion = arr1[0] + arr1[1] + arr1[2];
          var serviceVersion = arr[0] + arr[1] + arr[2];
          console.log(serviceVersion);
          if (Number(serviceVersion) > Number(currentVersion)) {
            _this.$store.state.appVersion = res.data.data.version;
            // 更新提示
            _this.showUpdataApp = true;
            _this.showVersionUpdate = true;
            _this.$store.state.uadataContent = res.data.data;
            _this.versionData = res.data.data;
            _this.appName = res.data.data.path;
          }
        } else {
          Toast.fail("服务器版本获取失败！");
        }
      }
    },
    toMapGelocation() {
      var mybounds = new AMap.Bounds(
        this.bounds.northeast.toString().split(","),
        this.bounds.southwest.toString().split(",")
      );
      // debugger
      map.setBounds(mybounds);
    },
    /**
     *  var mybounds = new AMap.Bounds([116.319665, 39.855919], [116.468324,39.9756]);
      map.setBounds(mybounds);
     */
    showBtn() {
      this.$store.state.btnStatus = !this.$store.state.btnStatus;
      if (this.$store.state.btnStatus === true) {
        this.transform = "";
      } else {
        this.transform = "opacity: 0; filter:Alpha(opacity=0)";
      }
    },
    clickToShow() {
      this.opened = true;
    },
    initAmap() {
      map = new AMap.Map("containerHome", {
        center: [113.81621, 22.631834],
        zoom: 10,
        zooms: [6, 15],
        mapStyle: "amap://styles/b23dc72b03be03f4dd932c413bc85c94",
        // resizeEnable: true,
        // rotateEnable: true,
        // pitchEnable: true,
        // // zoom: 17,
        // pitch: 60,
        // rotation: -15,
        // viewMode: "3D", //开启3D视图,默认为关闭
        buildingAnimation: true, //楼块出现是否带动画
        expandZoomRange: true
        // mapStyle: "amap://styles/normal"
      });

      // AMap.plugin("AMap.ToolBar", function() {
      //   var toolbar = new AMap.ToolBar();
      //   map.addControl(toolbar);
      // });
      AMap.event.addListener(map, "zoomend", function() {
        let scale = 20 - map.getZoom();
        // AMap.moveCamera(CameraUpdateFactory.zoomTo(12));
        // console.log(map.zoom);
        console.log(map.getZoom());
        // _this.scale = parseInt(Math.pow(2, scale));
      });
      // map.plugin(["AMap.Scale"], function() {
      //   var scale = new AMap.Scale({});
      //   map.addControl(scale);
      // });
      driving = new AMap.Driving({
        map: map,
        panel: "panel",
        ferry: 1
      });
      map.setCity("宝安区");
      // map.clear();
      this.bounds = map.getBounds();
      map.clearMap();

      // this.mapStaion();
    },
    async initData() {
      var url = "/station/water-station/map";
      var _this = this;
      var res = await AppBace.request(url, "GET", {}, this.token);
      console.log("map", res);
      this.listLng = res.data.data;
      this.showover = false;
      this.mobileStation = res.data.mobileStation;
      // debugger
    },
    initMapMarker() {
      var lng = "";
      var lat = "";
      var _this = this;
      var arr = this.listLng;
      // debugger;
      infoWindow = new AMap.InfoWindow({
        anchor: "bottom-center",
        offset: new AMap.Pixel(0, 0)
      });
      for (var i = 0; i < this.listLng.length; i++) {
        lng = arr[i].longitude;
        lat = arr[i].latitude;
        var systemStatus = arr[i].systemStatus;
        var warningStatus = "";
        var color = "";
        var warningList = [];
        var warningId = "";
        warningList = arr[i].warningList;
        if (warningList !== null && warningList.length > 0) {
          warningId = arr[i].warningList[0].warningId;
        } else {
          warningId = "";
        }
        switch (systemStatus) {
          case 0:
            warningStatus = "待处理";
            color = "red";
            break;
          case 2:
            warningStatus = "已处理";
            color = "green";
            break;
          case 1:
            warningStatus = "正在处理";
            color = "orange";
            break;
          default:
            warningStatus = "暂无";
            color = "";
            break;
        }
        var marker1 = new AMap.Marker({
          position: [lng, lat],
          icon: _this.filterIconStatus(arr[i], arr[i].waterInfo),
          // systemStatus
          // icon:
          //   arr[i].waterInfo === "超标"
          //     ? "static/img/mapMarkerNews1.png"
          //     : arr[i].waterInfo === "达标"
          //     ? "static/img/mapMarkerNews.png"
          //     : arr[i].waterInfo === "-"
          //     ? "static/img/mapMarkerNews3.png"
          //     : "static/img/mapMarkerNews2.png",
          imageSize: new AMap.Size(42, 26),
          map: map,
          offset: new AMap.Pixel(-10, -10)
        });

        var name = arr[i].stationName;
        var MyComponent = Vue.extend({
          template: MarkerPicStation.mapMarkerNormal(
            arr[i].stationName,
            arr[i].systemName,
            arr[i].waterInfo,
            arr[i].address,
            lng,
            lat,
            warningStatus,
            color,
            warningId,
            arr[i].deviceStatus
          ),
          data() {
            return {
              id: ""
            };
          },
          methods: {
            world: function(lng, lat) {
              console.log(_this, "MyComponent");
              //点击事件 使用 组件对象
              // console.log(id, "arr");
              // _this.rightShow = false;
              // _this.initDriving();
              _this.getposition(lng, lat);
            },
            async handleWarning(contentWarning) {
              // var arrWarning = contentWarning.split(",");

              var Id = arrWarning[0];
              var name = arrWarning[1];
              var status = arrWarning[2];
              _this.warningTitle = status;
              if (Id !== null) {
                for (var index = 0; index < arr.length; index++) {
                  var Warning = arr[index].warningList;

                  if (
                    Warning !== null &&
                    Warning !== undefined &&
                    Warning.length > 0
                  ) {
                    if (Id === arr[index].warningList[0].warningId) {
                      var warningData = [];
                      _this.warningData = [];
                      warningData = await _this.initWarningData(Id, name);

                      if (
                        warningData !== null &&
                        warningData.length !== undefined &&
                        warningData.length > 0
                      ) {
                        for (const key in warningData[0].warningContent) {
                          if (
                            warningData[0].warningContent.hasOwnProperty(key)
                          ) {
                            var cont = warningData[0].warningContent[key];
                            // name因子名称

                            var nameFactor = AppBace.warningDataHandle(key);

                            _this.warningData.push({
                              name: nameFactor,
                              value: cont
                            });
                          }
                        }
                        _this.showDialog = true;
                      }
                    }
                  }
                  // else {
                  //   Toast("未查询到预警信息！");
                  // }
                }
              }
            },
            toBim(content) {
              // var content = content.split(",");
              // var MNbim = content[0];
              // var stationBim = content[1];
              // console.log(MNbim);
              // // window.open("http://127.0.0.1:9527?MN=" + MN);
              // _this.$router.push({
              //   path: "iframePage",
              //   query: { MN: MNbim, station: stationBim }
              // });
            },
            LookReaData(e) {
              console.log(e);
              // _this.opened = true;
              _this.id = e;
              _this.$parent.showMap();
            },
            callStation(number) {
              console.log(number);
              //  window.open();
              // Dialog.alert({
              //   message: "即将拨打：" + number
              // }).then(() => {
              //   // on close
              //   window.location.href = "tel://" + number;
              // });
              Dialog.confirm({
                message: number,
                className: "dialogContent"
              })
                .then(() => {
                  // on confirm
                  window.location.href = "tel://" + number;
                })
                .catch(() => {
                  // on cancel
                });
            }
          }
        });
        var component = new MyComponent().$mount();
        marker1.content = arr[i];
        marker1.on("click", _this.MoveMarkerClick);
      }
    },

    MoveMarkerClick(e) {
      // console.log(e.target.getPosition());
      // this.opened = true;
      var content = `<div >
         ${e.target.content.stationName}
      </div>`;
      infoWindow.setContent(content);

      infoWindow.open(map, [
        e.target.content.longitude,
        e.target.content.latitude
      ]);
      // this.$parent.showMap();
      setTimeout(() => {
        this.$parent.showMap("", e.target.content);
      }, 100);
    },
    // 预警
    async initWarningData(e, name) {
      var url = "/warning/list";
      var waterStation = name,
        starttime = AppBace.DateTime(10),
        lasttime = AppBace.DateTime(-1),
        pageSize = 1,
        pageNum = 1;
      var res = await AppBace.request(
        url,
        "POST",
        {
          lasttime,
          pageNum,
          pageSize,
          starttime,
          waterStation
        },
        this.token
      );
      console.log(res);
      return res.data.data.data;
    },
    initCarMarker() {
      let _this = this;
      var lng = "";
      var lat = "";
      var arr = [];
      arr = this.mobileStation;
      infoWindow = new AMap.InfoWindow({
        anchor: "bottom-center",
        offset: new AMap.Pixel(0, 0)
      });
      for (var i = 0; i < arr.length; i++) {
        lng = arr[i].longitude;
        lat = arr[i].latitude;
        // if (arr[i].deviceStatus === 1) {
        var marker2 = new AMap.Marker({
          position: [lng, lat],
          icon: _this.filterIconStatusCar(arr[i], arr[i].waterInfo),
          map: map,
          offset: new AMap.Pixel(-10, -10)
        });
        var MyComponent = Vue.extend({
          template: MarkerPicStation.mapCarStatus(
            arr[i].name,
            arr[i].monitorSystem,
            arr[i].vehicleStatus,
            arr[i].deviceStatus,
            arr[i].address,
            lng,
            lat,
            arr[i].waterInfo
          ),
          methods: {
            world: function(lng, lat) {
              console.log(_this, "MyComponent");
              //点击事件 使用 组件对象
              // console.log(id, "arr");
              // _this.rightShow = false;
              // _this.initDriving();
              _this.getposition(lng, lat);
            }
          }
        });
        var component = new MyComponent().$mount();
        // marker2.content = component.$el;
        marker2.content = arr[i];
        // marker2.content =
        // } else {
        // }
        marker2.on("click", MoveMarkerClick);
        // marker2.emit("click", { target: marker2 });
        // map.setFitView(marker1);
        function MoveMarkerClick(e) {
          console.log(e.target.content);

          _this.openPointAddress = true;
          var content = `<div>${e.target.content.name} (移动水站)</div>`;
          infoWindow.setContent(content);
          infoWindow.open(map, [
            e.target.content.longitude,
            e.target.content.latitude
          ]);
          // Toast("移动水站暂时没有运行，请稍后！");
          setTimeout(() => {
            _this.$parent.showMap("", e.target.content);
          }, 100);
        }
        // map.setFitView(marker2);
      }
    },
    // 消息提醒
    noticeTo(name) {
      cordova.plugins.notification.local.schedule([
        {
          id: 0,
          title: "宝安区治水提质动态监测系统",

          text: name,

          foreground: true
        }
        // {
        //   id: 1,
        //   title: "版权所属(版本1.0.0)",

        //   text: "深圳安志生态环境有限公司",

        //   foreground: true
        // }
      ]);
    },
    initLine(arrLine) {
      // var path = arrLine;
      var arrPath = [];
      for (let index = 0; index < arrLine.length; index++) {
        var lat = arrLine[index].lat;
        var lng = arrLine[index].lng;
        arrPath.push([Number(lng), Number(lat)]);
      }
      this.path = arrPath;
      this.polyline = new AMap.Polyline({
        map: map,
        path: arrPath,
        // strokeColor: "red", //线颜色
        strokeColor: "#28F", //线颜色
        strokeOpacity: 0.8, //线透明度
        strokeWeight: 8, //线宽
        strokeStyle: "solid" //线样式
      });
      this.polyline.setMap(map);
      // 缩放地图到合适的视野级别
      // map.setFitView(this.polyline);
      // this.drawLine(arrPath);
    },
    initCar(position) {
      console.log("执行了initCar");
      this.carMarker = new AMap.Marker({
        // icon: "https://webapi.amap.com/images/car.png",
        icon: "static/img/carMapLine.png",
        offset: new AMap.Pixel(-26, -14),
        autoRotation: true,
        angle: -90
      });
      this.carMarker.setMap(map);

      this.carMarker.setPosition(position);
      this.carMarker.on("moving", e => {
        // console.log(e);
        this.polyline.setPath(this.path);
      });
      this.carMarkerCreateFlag = true;
      // map.setFitView();
    },
    moveCar(lnglat) {
      this.carMarker.moveTo(new AMap.LngLat(...lnglat), 200);
      // map.setFitView();Numbe
    },
    wesocketDataInfo() {
      var that = this;
      // this.myWebsocket = new WebSocket(
      //   "ws://47.112.16.168:8800/websocket/" + sessionStorage.getItem("goEsay")
      //   // "ws://192.168.50.151:8800/websocket/" + sessionStorage.getItem("goEsay")
      // );
      var path = this.pathWeb + sessionStorage.getItem("username");

      // 实例化socket
      myWebsocket = new WebSocket(path);
      // 监听socket连接
      myWebsocket.onopen = this.open;
      // 监听socket错误信息
      myWebsocket.onerror = this.error;
      // 监听socket消息
      myWebsocket.onmessage = this.getMessage;
      //监听socket断开的消息
      myWebsocket.onclose = this.onClose;
    },
    open: function() {
      console.log("socket连接成功");
      myWebsocket.send("warn");
      myWebsocket.send("task");

      setTimeout(() => {
        myWebsocket.send("warn");
        myWebsocket.send("task");
      }, 200);
      this.socket = false;
    },
    error: function() {
      console.log("连接错误");
    },
    getMessage: function(msg) {
      // console.log(msg.data);
      this.carToWebLine(msg.data);
    },
    onClose: function() {
      console.log("websocket关闭");
    },
    // 接收到websocket的数据推送然后进行数据处理
    carToWebLine(data) {
      var _this = this;
      // console.log(JSON.parse(data));

      var mapData = JSON.parse(data);
      // debugger
      if (mapData.type === "jingwei") {
        let MapList = mapData.pushMessage;
        let MapListlength = MapList.length;
        if (MapListlength > 1) {
          if (!this.carMarkerCreateFlag) {
            this.initCar([
              MapList[MapListlength - 2].lng,
              MapList[MapListlength - 2].lat
            ]);
            this.moveCar([
              MapList[MapListlength - 1].lng,
              MapList[MapListlength - 1].lat
            ]);
            this.initLine(MapList);
          }
        } else if (MapListlength === 1) {
          if (!this.carMarkerCreateFlag) {
            this.initCar([MapList[0].lng, MapList[0].lat]);
            this.moveCar([MapList[0].lng, MapList[0].lat]);
          }
        }
      }
      if (mapData.type === "point") {
        this.carStationPoint(mapData.pushMessage);
      }
      // 预警推送
      if (mapData.type === "因子超标预警") {
        // <van-notice-bar text="通知内容" left-icon="volume-o" />;
        // this.warningScoller =
        // var mapDataDevice = mapData.id;
        // var DataWarning = [];
        // mapData.push({
        //   color:""
        // })
        this.noticeTo("因子超标预警");
        if (this.DataDevice.indexOf(mapData.id) === -1) {
          this.DataDevice.push(mapData.id);
          this.DataWarning.push(mapData);
          return;
        }

        this.$store.commit("websocketWarningDataFuc", this.DataWarning);
        setTimeout(() => {
          _this.$refs.websocket.initWebSocket("预警提醒", this.DataWarning);
        }, 200);

        // this.$store.commit("websocketTaskDataFuc",this.DataWarning);
      }
      // 任务推送 mobile
      if (
        mapData.type === "mobile" ||
        mapData.type === "maintenance" ||
        mapData.type === "inspect"
      ) {
        if (this.DataTaskId.indexOf(mapData.taskId) === -1) {
          this.DataTaskId.push(mapData.taskId);
          this.DataTask.push(mapData);
          return;
        }
        this.$store.commit("websocketTaskDataFuc", this.DataTask);
        setTimeout(() => {
          _this.$refs.websocket.initWebSocket("任务推送", this.DataTask);
           _this.noticeTo("任务推送提醒！");
        }, 200);
      }
      // maintenance
      // if (mapData.type === 'maintenance') {

      // }
      // // inspect
      // if (mapData.type === 'inspect') {

      // }
    },
    // 车辆的实时点位信息；瞄点
    carStationPoint(pointData) {
      var lng = "";
      var lat = "";
      var arr = pointData;
      infoWindowStation = new AMap.InfoWindow({
        anchor: "bottom-center",
        offset: new AMap.Pixel(20, 0)
      });
      for (var i = 0; i < arr.length; i++) {
        lng = arr[i].longitude;
        lat = arr[i].latitude;
        if (true) {
          var markerStation = new AMap.Marker({
            position: [lng, lat],
            icon: "static/img/mapMarkerPoint.png",
            map: map,
            offset: new AMap.Pixel(-8, -8)
          });
          markerStation.content = MarkerPicStation.mapCarStatusNormal(arr[i]);
        } else {
          var markerStation = new AMap.Marker({
            position: [lng, lat],
            icon: "static/img/carRest.png",
            map: map,
            offset: new AMap.Pixel(-10, -10)
          });
          markerStation.content = MarkerPicStation.mapCarStatusNormal(arr[i]);
        }
        markerStation.on("click", MoveMarkerClick);
        // markerStation.emit("click", { target: markerStation });
        // map.setFitView(marker1);
        function MoveMarkerClick(e) {
          // console.log(e.lnglat.lat);
          infoWindowStation.setContent(e.target.content);
          infoWindowStation.open(map, e.target.getPosition());
        }
        // map.setFitView(marker2);
      }
    },
    drawLine(path) {
      if (path.length > 3) {
        // let map = this.map;
        this.polyline = new AMap.Polyline({
          map: map,
          path: path,
          showDir: true,
          strokeColor: "#28F", //线颜色
          strokeWeight: 6 //线宽
        });
        map.remove(this.polyline);
        this.polyline.setMap(map);
        console.log(JSON.parse(JSON.stringify(path)));
        this.polyline.setPath(JSON.parse(JSON.stringify(path)));
        this.polyline.setExtData({ extPath: path });

        // map.setFitView();
      }
    },
    initDriving(lnt, lat, longitude, latitude) {
      var _this = this;

      driving.searchOnAMAP({
        origin: new AMap.LngLat(lnt, lat),
        destination: new AMap.LngLat(longitude, latitude)
      });
    },
    getposition: function(lngCan, latCan) {
      var _this = this;
      navigator.vibrate(500);
      var me = {
        msg: ""
      };
      var lng = Number(lngCan);
      var lat = Number(latCan);
      var onSuccess = function(position) {
        console.log("position", position);
        var wgs84togcj02 = Coordtransform.wgs84togcj02(
          Number(position.coords.longitude),
          Number(position.coords.latitude)
        );
        console.log(wgs84togcj02);
        // _this.getGaoDE(wgs84togcj02[0], wgs84togcj02[1]);
        //   return arr;
        console.log(wgs84togcj02[0], wgs84togcj02[1], lng, lat);
        _this.initDriving(wgs84togcj02[0], wgs84togcj02[1], lng, lat);
      };
      var error = function(error) {
        me.msg =
          "code: " + error.code + "\n" + "message: " + error.message + "\n";

        Toast("定位失败，请确保你打开了手机GPS");
        console.log(me.msg);
      };

      navigator.geolocation.getCurrentPosition(onSuccess, error, {
        maximumAge: 30000,
        timeout: 30000,
        enableHighAccuracy: true
      });
    },
    getGaoDE(e, f) {
      var geocoder;
      var that = this;
      var lnglatXY = new AMap.LngLat(Number(e), Number(f));
      //加载地理编码插件
      AMap.plugin(["AMap.Geocoder"], function() {
        geocoder = new AMap.Geocoder({
          radius: 1000, //以已知坐标为中心点，radius为半径，返回范围内兴趣点和道路信息
          extensions: "all" //返回地址描述以及附近兴趣点和道路信息，默认"base"
        });
        //返回地理编码结果
        AMap.event.addListener(geocoder, "complete", geocoder_callBack);
        //逆地理编码
        geocoder.getAddress(lnglatXY);
      });
      function geocoder_callBack(params) {
        console.log("params", params);
        that.input.inputValue9 = params.regeocode.formattedAddress;
        // alert("当前" + params.regeocode.formattedAddress);
        // alert("状态" + params.info);
        // alert(params.type);
        // alert("数组" + params.regeocode, addressComponent);
      }
    },
    //定位
    mapStaion() {
      map.plugin("AMap.Geolocation", function() {
        var geolocation = new AMap.Geolocation({
          enableHighAccuracy: true, // 是否使用高精度定位，默认：true
          convert: true,
          showMarker: true,
          panToLocation: true,
          timeout: 10000
        });

        geolocation.getCurrentPosition();
        map.addControl(geolocation);
        AMap.event.addListener(geolocation, "complete", onComplete);
        AMap.event.addListener(geolocation, "error", onError);

        function onComplete(data) {
          // data是具体的定位信息
          window.alert("定位成功！");
        }

        function onError(data) {
          // 定位出错
          window.alert("定位出错！");
        }
      });
    },
    // 过滤器，返回icon
    filterIconStatus(arr, waterInfo) {
      // debugger;
      var icon = "";
      // switch (waterInfo) {
      //   case "超标":
      //     icon = "static/img/mapMarkerNews1.png";
      //     break;
      //   case "达标":
      //     icon = "static/img/mapMarkerNews.png";
      //     break;
      //   case "无水":
      //     if (arr.device == 1) {
      //       icon = "static/img/mapMarkerNews.png";
      //     } else {
      //       icon = "static/img/mapMarkerNews3.png";
      //     }
      //     break;
      //   case "黄色":
      //     icon = "static/img/mapMarkerNews2.png";
      //     break;

      //   default:
      //     icon = "static/img/mapMarkerNews3.png";
      //     break;
      // }
      switch (arr.icon.type) {
        case 0:
          icon = "static/img/mapMarkerNews.png";
          break;
        case 1:
          icon = "static/img/mapMarkerNews2.png";
          break;
        case 2:
          icon = "static/img/mapMarkerNews1.png";
          break;
        case 3:
          icon = "static/img/mapMarkerNews3.png";
          break;
      }
      return icon;
    },
    filterIconStatusCar(arr, waterInfo) {
      var icon = "";
      switch (arr.icon.type) {
        case 0:
          icon = "static/img/carOnline1.png";
          break;
        case 1:
          icon = "static/img/alitterare.png";
          break;
        case 2:
          icon = "static/img/carrate.png";
          break;
        case 3:
          icon = "static/img/carRest1.png";
          break;

        default:
          icon = "static/img/ing.png";
          break;
      }
      return icon;
    },
    // 刷新
    async flashData() {
      // myWebsocket.onclose = this.onClose;
      this.showover = true;
      // this.timerOut.clear();
      // await myWebsocket.close();

      // await this.initAmap();
      await this.initData();
      await this.initMapMarker();
      await this.initCarMarker();

      // await this.wesocketDataInfo();
      await this.upadataFunc();
      // var _this = this;
      // map.on("click", function(e) {
      //   infoWindow.close();
      //   _this.$parent.showMap(false, "");
      // });
    }
  },
  filters: {
    filterIcon(value) {}
  },
  destroyed() {
    if (!this.socket) {
      myWebsocket.close();
    }
    map && map.destroy();
    // document.removeEventListener("backbutton", onBackKeyDown, false);
  },
  async mounted() {
    // 开启一个返回按钮的监听
    this.showover = true;
    var _this = this;
    // console.log("router", this.$router.history.current.name);

    this.token = sessionStorage.getItem("token");
    // this.height = "height:" + (window.screen.height) + "px";
    this.height = "height:" + document.body.clientHeight + "px";
    // console.log(this.height);
    await this.initAmap();
    await this.initData();
    await this.initMapMarker();
    await this.initCarMarker();
    await this.wesocketDataInfo();
    map.on("click", function(e) {
      infoWindow.close();
      _this.$parent.showMap(false, "");
    });
    
    var  timerOut = setInterval(() => {
       this.flashData();
       console.log(1);
    }, 600000);
  },
  components: {
    LeftNavigation,
    RightNavigation,
    MapBottom,
    // HomeDataTable,
    faceBook,
    LeftView,
    RealDataComponent
  }
};
</script>
<style scoped>
#containerHome {
  width: 100%;
  height: 600px;
}
.MarkerLabel {
  position: absolute;
  top: 2%;
  background: white;
  /* padding: 10px; */
  border-radius: 10px;
  left: 10px;
  box-shadow: 2px 2px 2px #ccc;
  /* box-shadow: 2px 2px 2px #e2e2e2; */

  height: 113px;
}
.MarkerLabel img {
  width: 72px;
  height: 113px;
  border-radius: 10px;
}
.imageSize img {
}
.imageSize {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80px;
  font-size: 12px;
  margin-bottom: 10px;
  padding: 10px;
  height: 30px;
  color: rgb(117, 116, 116);
  border-bottom: 1px rgb(238, 238, 238) solid;
}
.positionAbsolute {
  position: absolute;
  top: 5%;
  left: 10px;
  opacity: 1;
  filter: Alpha(opacity=100);
  transition: opacity 2s;
}
.positionRight {
  position: absolute;
  top: 5%;
  right: 10px;
}
.bottomMap {
  position: absolute;
  bottom: 0;
  left: 50%;
  display: flex;
  justify-content: center;
}
.headerMap {
  height: 40px;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  padding: 10px;
  border-bottom: 1px #ccc solid;
}

#panel {
  position: fixed;
  background-color: white;
  max-height: 3%;
  overflow-y: auto;
  top: 0px;
  right: 0px;
  /* width: 100%; */
  /* height: 40px; */
}
#panel .amap-call {
  background-color: #009cf9;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
#panel .amap-lib-driving {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  overflow: hidden;
}
.bottomTable {
  height: 60px;
  display: flex;
  align-items: center;
  padding: 10px 10px;
  font-size: 14px;
  font-weight: 600;
  border-bottom: 8px solid rgb(236, 236, 236);
}
.positionTop {
  position: absolute;
  top: 0px;
  width: 100%;
}
.gelocation {
  position: absolute;
  bottom: 40%;
  right: 10px;
  box-shadow: 0px 0px 5px #ccc;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexDialog {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 20px;
  border: 1px solid rgb(250, 250, 250);
  font-size: 12px;
}
.BORDER {
  border-bottom: 1px #ccc solid;
  /* background: #009cf9;
  color: white; */
}
.imageSizeGELOCATION {
  display: flex;
  align-items: center;
}
.overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}
</style>



