<template>
  <div>
    <div v-for="(item,index) in itemDataCont" :key="index" class="paddingHome">
      <div class="backGround-back">
        <div class="heaiderDiv">
          <div class="contHeader">
            <span>站点名称</span>
            <span>{{item.station==='中途泵站'?item.station+'(移动监测站)':item.station}}</span>
          </div>
          <div class="contHeader">
            <span>监测时间</span>
            <span>{{item.datatime}}</span>
          </div>
        </div>

        <div class="headerTitle fontWeight">
          <div class="left">因子</div>
          <div class="center">浓度值</div>
          <div class="right">标准值</div>
        </div>

        <div class="headerTitle">
          <div class="left">
            <span>pH</span>
          </div>
          <div class="center">
            <span
              :style="(item.ph>$store.state.factor.ph1&&item.ph<$store.state.factor.ph)?'color:red':'color:green'"
            >{{item.ph===null?'--':item.ph}}</span>
          </div>
          <div class="right">{{$store.state.factor.ph}}~{{$store.state.factor.ph1}}</div>
        </div>

        <div class="headerTitle" v-show="item.station!==undefined&&item.station==='中途泵站'">
          <div class="left">
            <span>CODcr</span>
          </div>
          <div class="center">
            <span
              :style="item.cod>$store.state.factor.cod?'color:red':'color:green'"
            >{{item.cod===null?'--':item.cod+' '+'mg/L'}}</span>
          </div>
          <div class="right">{{$store.state.factor.cod+' '+'mg/L'}}</div>
        </div>

        <div class="headerTitle">
          <div class="left">
            <span>氨氮</span>
          </div>
          <div class="center">
            <span
              :style="item.ammoniaNitrogen>$store.state.factor.ammoniaNitrogen?'color:red':'color:green'"
            >{{item.ammoniaNitrogen===null?'--':item.ammoniaNitrogen+' '+'mg/L'}}</span>
          </div>
          <div class="right">{{$store.state.factor.ammoniaNitrogen+' '+'mg/L'}}</div>
        </div>

        <div class="headerTitle">
          <div class="left">
            <span>总氮</span>
          </div>
          <div class="center">
            <span
              :style="item.nitrogen>$store.state.factor.nitrogen?'color:red':'color:green'"
            >{{item.nitrogen===null?'--':item.nitrogen+' '+'mg/L'}}</span>
          </div>
          <div class="right">{{$store.state.factor.nitrogen+' '+'mg/L'}}</div>
        </div>

        <div class="headerTitle">
          <div class="left">
            <span>总磷</span>
          </div>
          <div class="center">
            <span
              :style="item.phosphorus>$store.state.factor.phosphorus?'color:red':'color:green'"
            >{{item.phosphorus===null?'--':item.phosphorus+' '+'mg/L'}}</span>
          </div>
          <div class="right">{{$store.state.factor.phosphorus+' '+'mg/L'}}</div>
        </div>

        <div class="headerTitle">
          <div class="left">
            <span>总铜</span>
          </div>
          <div class="center">
            <span
              :style="item.cuprum>$store.state.factor.cuprum?'color:red':'color:green'"
            >{{item.cuprum===null?'--':item.cuprum+' '+'mg/L'}}</span>
          </div>
          <div class="right">{{$store.state.factor.cuprum+' '+'mg/L'}}</div>
        </div>

        <div class="headerTitle">
          <div class="left">
            <span>总镍</span>
          </div>
          <div class="center">
            <span
              :style="item.nickel>$store.state.factor.nickel?'color:red':'color:green'"
            >{{item.nickel===null?'--':item.nickel+' '+'mg/L'}}</span>
          </div>
          <div class="right">{{$store.state.factor.nickel+' '+'mg/L'}}</div>
        </div>

        <div class="headerTitle">
          <div class="left">
            <span>总铬</span>
          </div>
          <div class="center">
            <span
              :style="item.chromium>$store.state.factor.chromium?'color:red':'color:green'"
            >{{item.chromium===null?'--':item.chromium+' '+'mg/L'}}</span>
          </div>
          <div class="right">{{$store.state.factor.chromium+' '+'mg/L'}}</div>
        </div>

        <div class="headerTitle">
          <div class="left">
            <span>总氰化物</span>
          </div>
          <div class="center">
            <span
              :style="item.prussiate>$store.state.factor.prussiate?'color:red':'color:green'"
            >{{item.prussiate===null?'--':item.prussiate+' '+'mg/L'}}</span>
          </div>
          <div class="right">{{$store.state.factor.prussiate+' '+'mg/L'}}</div>
        </div>
        <div class="headerTitle">
          <div class="left">
            <span>设备状态</span>
          </div>
          <div class="center"></div>
          <div class="right">
            <span
              :style="(item.systemStatus===0||item.systemStatus===null)?'color:red':'color:green'"
            >{{(item.systemStatus===0||item.systemStatus===null)?'异常':'正常'}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
export default {
  data() {
    return {
      itemDataCont: [],
      token: ""
    };
    
  },
  props:["id"],
  methods: {
     async initData(e){
       var stationName = this.id;
        var url = "/station/monitor-system/datas/app";
        this.token = sessionStorage.getItem("token");
        var res = await AppBace.request(url, "GET", {}, this.token);
        var arr = res.data.data;
        for (let index = 0; index < arr.length; index++) {
            var id = arr[index].station;
            if (stationName.slice(0,3)===id.slice(0,3)) {
                this.itemDataCont.push(arr[index]);
            }
            
        }
      }
  },
  mounted(){
      this.initData();
  }
};
</script>
<style scoped>
.backGround-back {
  background-color: white;
  border-radius: 10px;
  padding: 10px;
}
.paddingHome {
  padding: 5px 10px 5px 10px;
  border-radius: 10px;
}
.contHeader {
  font-weight: 600;
  font-size: 12px;
  height: 40px;
  /* border-bottom: 1px solid #ccc; */
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.contData {
  height: 40px;
  flex: 3;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 12px;
  padding: 10px 5px;
}
.flexContent {
  display: flex;
  flex-wrap: wrap;
}
.line {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ccc;
}
.headerTitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  font-size: 12px;
}
.headerTitle .left {
  /* width: 33.33%; */
  flex: 1;
  display: flex;
  justify-content: center;
}
.headerTitle .center {
  flex: 4;
  display: flex;
  justify-content: center;
}
.headerTitle .right {
  text-align: right;
  flex: 1;
  display: flex;
  justify-content: center;
}
.heaiderDiv {
  border-bottom: 1px solid #ccc;
}
.fontWeight {
  font-weight: 600;
  font-size: 12px;
}
</style>


