<template>
  <div class="boxContainer">
    <div
      class="boxTitle"
      v-for="(item,index) in menu"
      :key="index"
      :style="item.img"
      @click="NavigateTo(item.name)"
    >
      <div class="titleName">
        <span :style="'color:'+item.color">{{item.name}}</span>
      </div>
    </div>
  </div>
</template>
<script>
import MenuPermision from "../../../static/js/permisions";
import AppBace from "../../../static/ajax";
export default {
  data() {
    return {
      menu: [],
      token: "",
      transform: "opacity: 0; filter:Alpha(opacity=0)",
      vehicle: []
    };
  },
  methods: {
    showHide() {
      this.transform = "";
      setTimeout(() => {
        this.transform = "opacity: 0; filter:Alpha(opacity=0)";
      }, 15000);
    },
    NavigateTo(e) {
      var router = "";
      switch (e) {
        case "历史数据":
          router = "/ToHistory";
          break;
        case "实时地图":
          router = "/Map";
          break;
        case "辅助决策":
          router = "/AssistantMenu";
          break;
        case "运维列表":
          router = "/controlList";
          break;
        case "预警管理":
          router = "/warning";
          break;
        case "信息公开":
          router = "/Event";
          break;
        case "报告报表":
          router = "/Reports";
          break;
        case "监测调度":
          router = "/MonitoringDispatchingAdd";
          break;
        case "维护维修":
          router = "maintenance";
          break;
        case "日常管理":
          router = "DayControl";
          break;
        case "水环境评价":
          router = "waterPollution";
          break;
        case "水环境排名":
          router = "ranking";
          break;
        case "污染溯源":
          router = "NewAddPollution";
          break;
        case "实时数据":
          router = "realDataHome";
          break;
        case "废液管理":
          router = "WasteLiquidManagement";
          break;
        case "设备列表":
          router = "DeviceList";
          break;
        case "运维管理":
          router = "ControlMaintain";
          break;
        // realDataHome
        default:
          break;
      }
      this.$router.push(router);
    },

    initMenuLeft() {
      var user = sessionStorage.getItem("user");
      this.menu = MenuPermision.userMenu(user);
    },
    async menuPoint() {
      // this.fullscreen();
      // this.token = sessionStorage.getItem("token");
      var url = "/station/water-station/nameList";

      var res = await AppBace.request(url, "GET", {}, this.token);
      // console.log(res);
      // this.options = res.data.data;
      this.$store.commit("deleteDataMenu", res.data.data);
      console.log("menu", this.$store.state.menu);
    },
    async initDataenterprise() {
      var url = "/station/file/pollutant";

      var res = await AppBace.request(url, "GET", {}, this.token);
      console.log(res);
      var arr = [];
      arr = res.data.data;
      var array = [];
      for (var index = 0; index < arr.length; index++) {
        const element = arr[index].pollutantName;
        array.push(element);
      }
      this.$store.commit("enterprise", array);
      // debugger
    },
    async vehicleListInit() {
      var url = "/job/vehicle";
      var res = await AppBace.request(url, "GET", {}, this.token);
      var vehicleArr = [];
      vehicleArr = res.data.data;
      for (let index = 0; index < vehicleArr.length; index++) {
        var element = vehicleArr[index].name;
        this.vehicle.push(element);
      }
      this.$store.commit("vehicleListContent", this.vehicle);
      console.log("vehicle", this.$store.state);
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.initMenuLeft();
    this.menuPoint();
    this.initDataenterprise();
    this.vehicleListInit();
  }
};
</script>
<style scoped>
.flexMenu {
  display: flex;
  flex-direction: row;
  /* border-radius: 10px; */
  margin-bottom: 10px;
  /* box-shadow: 5px 5px 5px #ccc; */
}
.flexMenu:active {
  -webkit-tap-highlight-color: transparent;
  opacity: 0.5;
}
.backLeftBtn {
  /* background: white; */
  border-radius: 10px;
  opacity: 1;
  filter: Alpha(opacity=100);
  transition: opacity 2s;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.imageSize img {
  width: 36px;
  height: 36px;
}
.menuBtn {
  /* padding: 10px; */
  height: 60px;
  width: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  background: white;
  /* justify-content: space-between; */
  /* opacity: 0.7; */
  /* box-shadow: 5px 5px 5px #ccc; */
  /* box-shadow: 2px 2px 2px #e2e2e2; */

  font-size: 10px;
}
.pointer {
  position: absolute;
  height: 3.8rem;
  bottom: 3rem;
  width: 100%;
}
.boxTitle {
  height: 100px;
  width: 49%;
  background: white;
  border-radius: 10px;
  margin-bottom: 10px;
  box-shadow: 0 0 5px #ccc;
  /* padding: 10px; */
}
.imagePic img {
  width: 100%;
  height: 100px;
}
.boxContainer {
  /* padding: 0 10px 10px 10px; */
  border-radius: 10px;
  /* background: white; */
  margin-bottom: 10px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
}
.titleName {
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  font-size: 16px;
}
</style>



