<template>
  <div>
    <!-- <mu-appbar style="width: 100%;" color="#4ed995" class="header">
      <mu-button icon slot="left" class="icon" size="20" @click="toHome()">
        <mu-icon value="keyboard_arrow_left"></mu-icon>
      </mu-button>
     
      <span>实时地图</span>

      <mu-button icon slot="right" class="icon" size="20" @click="ToChildComponents()" >
        <img src="static/img/queryNews.png" alt srcset />
      </mu-button>

      <mu-button icon  class="icon" size="20">
        <mu-icon value="add"></mu-icon>
      </mu-button>
    </mu-appbar> -->
    <mu-container style="padding:0">
      <mu-tabs :value.sync="active2" color="white" indicator-color="green" full-width>
        <mu-tab style="color:black">水站监控</mu-tab>
        <mu-tab style="color:black">移动监测</mu-tab>
      </mu-tabs>
      <div class="demo-text" v-if="active2 === 0" :style="height">
        <vueMap />
        <!-- 1 -->
      </div>
      <div class="demo-text" v-if="active2 === 1" :style="height">
        <vueMapPoline ref="mychild" />
      </div>
    </mu-container>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import vueMap from "../../components/map/realMap";
import vueMapPoline from "../../components/map/index";
import HTTP from "axios";
let map = "";
export default {
  name: "TestMap",
  data() {
    return {
      active2: 0,
      open: false,
      height:""
    };
  },
  methods: {
    toHome() {
      this.$router.push({ path: "/Navigation", query: { id: 0 } });
      // this.$router.go(-1);
    },
    ToChildComponents() {
      this.open = true;
      // console.log(this.open);
      // this.$emit('event',this.open)//将值绑定到transfer上传递过去
      this.$refs.mychild.childClick();
    },

  },
  mounted(){
    this.height = "height:" + (window.screen.height - 104) + "px";
  },
  components: {
    vueMap,
    vueMapPoline
  }
};
</script>
<style scoped>
.icon img {
  width: 28px;
  height: 28px;
}
</style>



