<template>
  <div>
    <div class="boxFlex">
      <div class="flexMenu" @click="alertTo()">
        <div class="imageSize">
          <img src="../../../static/imgs/units.png" alt />
        </div>
        <div class="flexContent">
          <div class="flexCenter">站点数量</div>
          <div class="flexCenter">
            {{itemImageContent.totalCount}}
            <span>个</span>
          </div>
        </div>
      </div>
      <div class="flexMenu" @click="excessStations()">
        <div class="imageSize">
          <img src="../../../static/imgs/Exceeding.png" alt />
        </div>
        <div class="flexContent">
          <div class="flexCenter">超标站点</div>
          <div class="flexCenter">
            {{itemImageContent.excessCount}}
            <span>个</span>
          </div>
        </div>
      </div>
      <div class="flexMenu">
        <div class="imageSize">
          <img src="../../../static/imgs/Compliancerate.png" alt />
        </div>
        <div class="flexContent">
          <div class="flexCenter">达标率</div>
          <div class="flexCenter">
            {{(itemImageContent.complianceRate*100).toFixed(1)}}
            <span>%</span>
          </div>
        </div>
      </div>
      <div class="flexMenu" @click="toDevice()" v-show="user!=='manager'">
        <div class="imageSize">
          <img src="../../../static/imgs/failure.png" alt />
        </div>
        <div class="flexContent">
          <div class="flexCenter">设备故障</div>
          <div class="flexCenter">
            {{itemImageContent.deviceFailureCount}}
            <span>个</span>
          </div>
        </div>
      </div>
      <div class="flexMenu" v-show="user!=='manager'">
        <div class="imageSize">
          <img src="../../../static/imgs/rate.png" alt />
        </div>
        <div class="flexContent">
          <div class="flexCenter">故障率</div>
          <div class="flexCenter">
            {{(itemImageContent.deviceAbnormalRate*100).toFixed(1)}}
            <span>%</span>
          </div>
        </div>
      </div>
      <div class="flexMenu" v-show="user!=='manager'">
        <div class="imageSize">
          <img src="../../../static/imgs/Effective.png" alt />
        </div>
        <div class="flexContent">
          <div class="flexCenter">有效数据量</div>
          <div class="flexCenter">
            {{itemImageContent.effectiveDataCount}}
            <span>条</span>
          </div>
        </div>
      </div>
    </div>
    <van-overlay :show="show" @click="()=>{
         show = false;
      }">
      <div class="wrapper">
        <div class="block">
          <!-- <div class="headerTitle">
            <span>站点数量</span>
            <span>9个</span>
          </div>-->
          <div class="headerTitle backgroundNaviBar">
            <span style="flex:1">站点名称</span>

            <span style="flex:2">地址</span>
          </div>
          <div v-for="(item,index) in  itemStationList" :key="index" class="flexContentDIALOG border-bottom">
            <div>{{item.stationName==='福永水质净化厂前池'?item.stationName.slice(0,7):item.stationName}}</div>
            <div id="imageSizeMapNavi" @click="naviTo(item)" style="flex: 2;">
              <span>{{item.address}}</span>
              <img src="../../../static/img/NaviTo.png" alt />
            </div>
          </div>
        </div>
      </div>
    </van-overlay>
    <van-action-sheet style="z-index:9999" @select="selectMap" v-model="$store.state.showGaode" :actions="actions" cancel-text="取消" @cancel="onCancel" />
  </div>
</template>
<script>
import Vue from "vue";
import { Toast } from "vant";
import { Overlay } from "vant";
Vue.use(Overlay);
import AppBace from "../../../static/ajax";
import GaoDe from "../../../static/js/toGaodeMap/index";
export default {
  data() {
    return {
      itemImageContent: [],
      token: "",
      show: false,
      itemStationList: [1],
      showGaode:false,
       actions: [
        { name: '高德地图', color: '#07c160' }
      ],
      selectMapData:[],
      user:""
    };
  },
  async created() {
    this.token = sessionStorage.getItem("token");
    this.user = sessionStorage.getItem('user');
    // debugger
    console.log("user",this.user);
    var url = "/station/water-station/map";
    //   var _this = this;
    var res = await AppBace.request(url, "GET", {}, this.token);
    this.itemImageContent = res.data.mapInfo;
    // console.log("mapInfo", res);
  },
  methods: {
    onCancel() {
      this.$store.state.showGaode = false;
      // Toast('cancel');
    },
    toDevice() {
      this.$router.push("rateDevices");
    },
    alertTo() {
      // Toast("这只是一个数据统计，二期开发详情！");
      this.show = true;
      this.stationList();
    },
    // 水站列表
    // /station/water-station/nameList
    async stationList() {
      var url = "/station/water-station/stationList";
      var res = await AppBace.request(url, "GET", {}, this.token);
      console.log("nameList", res);
      var array = res.data.data;
      var arr = [];
      for (let index = 0; index < array.length; index++) {
        var stationName = array[index].name;
        var latitude = array[index].latitude;
        var longitude = array[index].longitude;
        var address = array[index].address;
        arr.push({
          stationName,
          latitude,
          longitude,
          address
        });
      }
      this.itemStationList = arr;
      
    },
    // 超标站点
    // /station/water-station/excessStations
    excessStations() {
      this.$router.push("excessStations");
      // this.show = true;
      // var url = "/station/water-station/excessStations";
      // var res = await AppBace.request(url, "GET", {}, this.token);
      // console.log("超标站点", res);
    },
    naviTo(res) {
      console.log(res);
      // GaoDe.toGaoDeMap();
      this.$store.state.showGaode = true;
      this.selectMapData = res;
      // this.$parent.TabFunc(false);
    },
    selectMap(item){
      var dataContent =  this.selectMapData;
      if (item.name==="高德地图") {
        GaoDe.toGaoDeMap("","","",dataContent.latitude,dataContent.longitude,dataContent.address);
      }
    }
  }
};
</script>
<style scoped>
.flexMenu {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: white;
  padding: 5px;
  border-radius: 10px;
  /* box-shadow: 2px 2px 2px #ccc; */
  margin-bottom: 10px;
  font-size: 10px;
  width: 30.33%;
  /* height: 80px; */
}
.imageSize img {
  width: 32px;
  height: 32px;
}
.imageSize {
  /* flex: 1; */
  display: flex;
  justify-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  font-size: 10px;
}
.flexContent {
  /* flex: 1; */
  display: flex;
  flex-direction: column;
  padding-top: 2px;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  
}
.block {
  width: 300px;
  /* height: 200px; */
  background-color: #fff;
  border-radius: 10px;
}
.headerTitle {
  /* background-color: #3572fe; */
  color: white;
  font-size: 12px;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  /* padding: 0 10px; */
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.headerTitle span {
  flex: 1;
  text-align: center;
}
.headerContentTitle {
  display: flex;
  flex-direction: row;
  font-size: 12px;
  font-weight: 600;
  border-bottom: 2px solid #ccc;
  justify-content: space-between;
  /* border-top-left-radius: 10px; */
  /* padding: 0 10px; */
}
.headerContentTitle div {
  /* width: 100px; */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  flex: 1;
}
.flexContentDIALOG {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px rgb(240, 239, 239) solid;
  /* padding: 0 10px; */
  height: 60px;
  padding: 10px;
  
  
}
.flexContentDIALOG div {
  /* width: 100px; */
  display: flex;
  font-size: 10px;
  justify-content: center;
  flex: 1;
  align-items: center;
  /* padding: 5px; */
}
#imageSizeMapNavi img {
  width: 20px;
  height: 20px;
  margin-left: 10px;
}
#imageSizeMapNavi {
  font-size: 10px;
  color: rgb(128, 186, 252);
  padding:0 10px;
  display: flex;
  justify-content: space-between;
  
}
.border-bottom{
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.boxFlex{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
</style>


