<template>
  <div>
    <div class="flexContentMenu" v-for="(item,index) in btnImageArr" :key="index">
      <!-- <a href="https://uri.amap.com/marker?position=经度,纬度&name=所在的位置名称">高德地图</a> -->
      <div class="imageSize" @click="naviTo(item)">
        <img :src="item.img" alt />
      </div>
    </div>
  </div>
</template>
<script>
import { Dialog } from "vant";
import { Toast } from "vant";
export default {
  data() {
    return {
      btnImageArr: [
        // { img: "static/img/settingsBottom.png", name: "设置" },
        { img: "static/img/taskNews1.png", name: "任务推送" },
        { img: "static/img/newsNo1.png", name: "预警提醒" },
        { img: "static/img/flash.png", name: "刷新" },
        // { img: "static/img/feedBack.png", name: "用户反馈" },
        // { img: "static/img/toOther.png", name: "切换用户" }
      ]
    };
  },
  props: ["warningData", "taskData"],
  created() {
    // console.log(this.warningData);
    // if (this.warningData.length > 0) {
    //   this.initWebSocket("预警提醒");
    //   debugger
    // }
  },
  methods: {
    naviTo(value) {
      var router = "";
      switch (value.name) {
        case "设置":
          router = "Settings";
          break;
        case "任务推送":
          if (value.img === "static/img/taskNewsYes1.png") {
            // console.log(1);
            router = "webSocketTask";
          } else {
            Toast("暂无任务推送");
          }
          // this.initWebSocket();
          break;
        case "预警提醒":
          if (value.img === "static/img/newsYes1.png") {
            // this.$router.push("webSocketWarning");
            router = "webSocketWarning";
          } else {
            Toast("暂无预警提醒");
          }
          //   router = "Settings";
          break;
        case "切换用户":
          //   router = "Settings";
          this.changeUser();
          break;
          case "刷新":
          //   router = "Settings";
          // this.$parent.initAmap();
          // this.$parent.initData();
          this.$parent.flashData();
          break;
        default:
          router = "Feedback";
          break;
      }
      this.$router.push(router);
    },
    // websocket
    initWebSocket(e, res) {
      // var e = "任务推送";
      var array = this.btnImageArr;
      for (let index = 0; index < array.length; index++) {
        var name = array[index].name;
        if (e === "任务推送") {
          if (name === e) {
            array[index].img = "static/img/taskNewsYes1.png";
          }
        }
        if (e === "预警提醒") {
          if (name === e) {
            array[index].img = "static/img/newsYes1.png";
          }
        }
      }
    },
    // 切换用户
    changeUser() {
      var _this = this;
      Dialog.confirm({
        title: "注销",
        message: "是否退出当前用户？"
      })
        .then(() => {
          // on confirm
          _this.$router.replace("/");
          localStorage.clear();
          sessionStorage.clear();
        })
        .catch(() => {
          // on cancel
        });
    }
  }
};
</script>
<style scoped>
.flexContentMenu {
  display: flex;
  background: white;
  border-radius: 20px;
  width: 40px;
  height: 40px;
  margin-bottom: 20px;
  justify-content: center;
  align-items: center;
  box-shadow: 2px 2px 2px #e2e2e2;
}
.imageSize img {
  height: 20px;
  width: 20px;
}
.imageSize {
  height: 20px;
}
.flexContentMenu:active {
  -webkit-tap-highlight-color: transparent;
  opacity: 0.5;
}
</style>


