<template>
  <div>
    <div id="containerHome" :style="height"></div>
    <!-- <mu-flex justify-content="center" class="fixed">
      <mu-button @click="openAlertDialog" style="z-index:9999">筛选路线</mu-button>
    </mu-flex>-->
    <mu-container>
      <mu-dialog
        title="请选择您的筛选项！"
        width="600"
        max-width="100%"
        :esc-press-close="false"
        :overlay-close="false"
        :open.sync="openAlert"
      >
        <div>
          <div>
            <mu-row class="bottomBorder">
              <mu-col span="4" class="borderCenter">
                <span>开始时间</span>
              </mu-col>
              <mu-col span="8">
                <mu-date-input
                  v-model="value7"
                  view-type="list"
                  no-display
                  container="bottomSheet"
                  type="date"
                  label-float
                  full-width
                ></mu-date-input>
              </mu-col>
            </mu-row>
            <mu-row class="bottomBorder">
              <mu-col span="4" class="borderCenter">
                <span>结束时间</span>
              </mu-col>
              <mu-col span="8">
                <mu-date-input
                  v-model="value8"
                  view-type="list"
                  no-display
                  container="bottomSheet"
                  type="date"
                  label-float
                  full-width
                ></mu-date-input>
              </mu-col>
            </mu-row>
            <mu-row class="bottomBorder">
              <mu-col span="4" class="borderCenter">
                <span>选择车辆</span>
              </mu-col>
              <mu-col span="8">
                <mu-select label v-model="normal.value1" full-width>
                  <mu-option
                    v-for="(option,index) in options"
                    :key="index"
                    :label="option"
                    :value="option"
                  ></mu-option>
                </mu-select>
              </mu-col>
            </mu-row>
          </div>
        </div>
        <mu-button slot="actions" flat color="primary" @click="closeAlertDialog">取消</mu-button>
        <mu-button slot="actions" flat color="primary" @click="confirm">确认</mu-button>
      </mu-dialog>
    </mu-container>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import MarkerCont from "../../../static/js/map/markerCont";
let map = "";
let array = [];
export default {
  name: "realMapComponents",
  data() {
    return {
      value7: "2019-08-01",
      value8: "2019-08-06",
      height: "",
      listLng: [],
      listContent: [],
      lineArr: [],
      openAlert: false,
      normal: {
        value1: ""
      },
      options: [],
      token: ""
    };
  },
  // props:['open'],
  methods: {
    initAmap() {
      map = new AMap.Map("containerHome", {
        center: [113.941146, 22.527268],
        zoom: 11,
        zooms: [10, 15],
        mapStyle: "amap://styles/b23dc72b03be03f4dd932c413bc85c94"
        // viewMode:'3D' // 地图模式
      });
      AMap.plugin("AMap.ToolBar", function() {
        var toolbar = new AMap.ToolBar();
        map.addControl(toolbar);
      });
    },
    async initDataXIE() {
      map.clearMap();
      console.log("xie");
      var url = "/mobile/routing";
      var pageNum = 1;
      var pageSize = 100000000;
      if (this.value7 !== "" && this.value8 !== "") {
        var startTime = AppBace.filter(this.value7);
        var endTime = AppBace.filter(this.value8);
      } else {
        var startTime = AppBace.filter(new Date());
        var endTime = AppBace.filter(new Date());
      }
      var vehicleId = this.normal.value1;
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageNum,
          pageSize,
          startTime,
          endTime,
          vehicleId
        },
        this.token
      );
      console.log("xie", res);
      this.listContent = res.data.data.rows;
    },
    mapMarker() {
      // debugger;

      var arr = [];
      arr = this.listContent;
      var _this = this;
      var lng = "";
      var lat = "";
      var infoWindow = new AMap.InfoWindow({ offset: new AMap.Pixel(0, -30) });
      for (let index = 0; index < arr.length; index++) {
        // const element = array[index];
        var monitoringDataList = arr[index].monitoringDataList;
        for (var i = 0; i < monitoringDataList.length; i++) {
          lng = monitoringDataList[i].longitude;
          lat = monitoringDataList[i].latitude;
          // debugger
          var marker1 = new AMap.Marker({
            position: [lng, lat],
            icon: "static/img/loserBlue1.png",
            // map: map,
            offset: new AMap.Pixel(-24, -22)
          });
          var test = 123;
          marker1.content = MarkerCont.mapCarContent(
            lng,
            lat,
            monitoringDataList[i].pollutionIndex,
            monitoringDataList[i].pollutantName,
            monitoringDataList[i].waterStatus,
            arr[index].person,
            arr[index].vehicleId,
            monitoringDataList[i].routingName,
            i
          );
          map.add(marker1);
          marker1.on("click", MoveMarkerClick);
          marker1.emit("click", { target: marker1 });
          function MoveMarkerClick(e) {
            infoWindow.setContent(e.target.content);
            infoWindow.open(map, e.target.getPosition());
          }
          _this.lineArr.push([lng, lat]);
          // debugger;
        }
        var marker = new AMap.Marker({
          map: map,
          position: [113.941146, 22.527268],
          // icon: "https://webapi.amap.com/images/car.png",
          icon: "static/img/carMapLine.png",
          offset: new AMap.Pixel(-26, -13),
          autoRotation: true,
          angle: -90,
          imageSize: new AMap.Size(8, 8)
        });
        var polyline = new AMap.Polyline({
          map: map,
          path: _this.lineArr,
          showDir: true,
          strokeColor: "#28F", //线颜色
          // strokeOpacity: 1,     //线透明度
          strokeWeight: 6 //线宽
          // strokeStyle: "solid"  //线样式
        });
        marker.on("moving", function(e) {
          // passedPolyline.setPath(e.passedPath);
        });

        marker.moveAlong(this.lineArr, 200);
        setInterval(() => {
          marker.resumeMove();
        }, 10000);
        marker.resumeMove();
        var startPoint = new AMap.LngLat(116.480983, 39.989628);
        var endPoint = new AMap.LngLat(116.43397, 39.934057);
        _this.lineArr = [];
      }
    },
    queryPoline() {},
    childClick() {
      this.openAlert = true;
    },
    closeAlertDialog() {
      this.value7 = "";
      this.value8 = "";
      this.normal.value1 = "";
      this.openAlert = false;
    },
    async confirm() {
      this.token = sessionStorage.getItem("token");
      await this.initDataXIE();
      this.openAlert = false;
      await this.mapMarker();
    }
  },
  async mounted() {
    // console.log(this.props.open);
    this.height = "height:" + (window.screen.height - 104) + "px";
    this.token = sessionStorage.getItem("token");
    this.options = this.$store.state.vehicleList;
    console.log(this.height);
    await this.initAmap();
    await this.initDataXIE();
    await this.mapMarker();
  }
};
</script>
<style scoped>
#containerHome {
  width: 100%;
  height: 600px;
}
.fixed {
  position: fixed;
  bottom: 0;
  right: 0;
}
.mu-dialog-title {
  font-size: 16px;
}
.bottomBorder {
  /* border-bottom: 1px #ccc solid; */
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 0 5px;
  margin-top: 5px;
  height: 40px;
}
.mu-input-line {
  border-bottom: #ccc 1px solid;
}
.borderCenter {
  display: flex;
  align-items: center;
  height: 40px;
}
</style>



